import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';

// services
import { HttpService } from './../../services/http.service';
import { api } from './../../services/api';

// models
import { IUser } from './../models';

@Injectable()
export class ProfileService {

  constructor(
    private httpService: HttpService
  ) { }

  public getCountries(): Observable<any> {
    return this.httpService.GetRequest(api.getCountries);
  }

  public getStates(countryId: number): Observable<any> {
    return this.httpService.GetRequest(api.getStates + countryId);
  }

  public updateUserProfile(userProfile: IUser): Observable<any> {
    return this.httpService.PutRequest(api.updateUserProfile, userProfile);
  }

}
