import { Injectable } from '@angular/core';
import { api } from '../api';
import {Observable} from 'rxjs'
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { HttpService } from '../http.service';
import { HttpClient } from '@angular/common/http';
import { from } from 'rxjs';
import { Idle } from '@ng-idle/core';
import { Router } from '@angular/router';
import { ToasterService } from '../toaster.service';
import { LoginComponent } from 'src/app/components/login/login.component';
import { LoginService } from '../loginService/login.service';
// import { HttpService } from './http.service';


@Injectable()
export class MPOAService {
  assetGroup$: BehaviorSubject<any> = new BehaviorSubject([]);
  mpoaList$: BehaviorSubject<any> = new BehaviorSubject([]);
  mpoaStep1$: BehaviorSubject<any> = new BehaviorSubject([]);

   base = 'https://dev.clocr.com';
   sample : any = "assas";
   //sidnav : any = false;
  public sidnav: any = false;

  constructor(private httpService : HttpService, 
    private idle: Idle, 
    private loginService : LoginService,
    private router: Router,
    private toaster: ToasterService) { }
    
  saveMpoa(mpoaObj: object) {
    this.httpService.PostRequest(api.saveMpoa, mpoaObj).subscribe(res => {
      if (res['success']) {
        console.log(res['data']);
        this.toaster.showSuccess('MPoA successfully added');
        return this.router.navigate(['/mpoa/' + res['data'].id]);
      } else {
        if (res['data'] == 'logout') {
          this.loginService.logout();
        }
        this.toaster.showError(res['error']);
      }
    });

  }
  
  getMpoaListByState(name) {
    this.httpService.PostRequest(api.getMpoaList, { name: name }).subscribe(res => {
      if (res['success']) {
        this.mpoaList$.next(res['data']);

      } else {
        if (res['data'] == 'logout') {
          this.loginService.logout();
        }

      }
    });
  }
  

}
