import { Injectable } from '@angular/core';
import { api } from '../api';
import { Observable } from 'rxjs'
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { HttpService } from '../http.service';
import { HttpClient } from '@angular/common/http';
import { from, of } from 'rxjs';
import { ToasterService } from '../toaster.service';
import { Router } from '@angular/router';
import { Idle } from '@ng-idle/core';
import { LoginService } from '../loginService/login.service';
import { catchError } from "rxjs/operators";
// import { HttpService } from './http.service';


@Injectable()
export class AssetService {
  assetGroup$: BehaviorSubject<any> = new BehaviorSubject([]);
  assets$: BehaviorSubject<any> = new BehaviorSubject([]);
  allassets$: BehaviorSubject<any> = new BehaviorSubject([]);
  assetDocs$: BehaviorSubject<any> = new BehaviorSubject([]);

  base = 'https://dev.clocr.com';
  sample: any = "assas";
  //sidnav : any = false;
  public sidnav: any = false;


  //   constructor(private httpService : HttpService,private toaster: ToasterService) { }
  constructor(private router: Router,
    private httpService: HttpService,
    private idle: Idle,
    private loginService: LoginService,
    private toaster: ToasterService) {
  }




  getAssetGroup() {
    this.httpService.GetRequest(api.getAssetGroup)
      .subscribe(res => {
        if (res['success']) {
          this.assetGroup$.next(res['data']);
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }

        }
      });
  }

  getDrawersGroup() {
    return this.httpService.GetRequest(api.getAssetGroup);
  }

  getGridAssetGroup() {
    return this.httpService.GetRequest(api.getAssetGroup).pipe(
      data => data,
      catchError(error => of(error))
    );;
  }

  getGridAllAssets() {
    return this.httpService.GetRequest(api.allAsset).pipe(
      data => data,
      catchError(error => of(error))
    );
  }

  getAssets() {
    return this.httpService.GetRequest(api.allAsset);
  }

  moveAsset(i, id, groupId) {
    this.httpService.PutRequest(api.moveAsset + id, { 'asset_group_id': groupId })
      .subscribe(res => {
        if (res['success']) {
          const assets = this.assets$.getValue();
          assets.asset.splice(i, 1);

          this.assets$.next(assets);
          this.toaster.showSuccess('Account successfully moved');

        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
          this.toaster.showError('Something went wrong');
        }
      }, err => {
        this.toaster.showError('Something went wrong');
        console.error('err', err);
      });
  }


  saveAssetGroup(assetGroupObj: object) {
    this.httpService.PostRequest(api.saveAssetGroup, assetGroupObj).subscribe(res => {
      if (res['success']) {
        const assetGroup = this.assetGroup$.getValue();

        assetGroup.push(res['data']);
        this.assetGroup$.next(assetGroup);
        this.toaster.showSuccess('Drawers successfully added');
      } else {
        if (res['data'] == 'logout') {
          this.loginService.logout();
        }
        this.toaster.showError(res['error']);
      }
    }, err => {
      const errObj = JSON.parse(err._body);
      this.toaster.showError(errObj.error);
      console.error('err', err);
    });

  }

  editSaveAssetGroup(assetObj, id, index) {
    this.httpService.PutRequest(api.editSaveAssetGroup + id, assetObj)
      .subscribe(res => {
        if (res['success']) {
          const assetGroup = this.assetGroup$.getValue();
          assetGroup[index] = res['data'];
          this.assetGroup$.next(assetGroup);
          this.toaster.showSuccess('Drawers successfully updated');
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
          this.toaster.showError('Something went wrong');
        }
      }, err => {
        this.toaster.showError('Something went wrong');
        console.error('err', err);
      });
  }

  deleteAssetGroup(id, i) {
    this.httpService.DeleteRequest(api.deleteAssetGroup + id)
      .subscribe(res => {
        if (res['success']) {

          const assetGroup = this.assetGroup$.getValue();
          assetGroup.splice(i, 1);
          this.assetGroup$.next(assetGroup);
          this.toaster.showSuccess('Drawers successfully removed');
          console.log("grp", this.assetGroup$.getValue());
        } else if (!res['success']) {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }

        }
      }, err => {
        // const errObj = JSON.parse(err._body);
        this.toaster.showError('Drawer can not be deleted. You have to delete or move the accounts associated with this Drawer');

      });
  }

  saveAsset(assetObj: object) {
    this.httpService.PostRequest(api.saveAsset, assetObj).subscribe(res => {
      if (res['success']) {
        this.getAllAssetsByGroup(assetObj['asset_group_id']);
        /*
         const assets = this.assets$.getValue();
         assets.push(res['data']);
         this.assets$.next(assets);
         */
        this.toaster.showSuccess('Account successfully added');
        console.log(assetObj['asset_group_id']);
        this.assetDocs$.next(null);
        this.router.navigate(['/asset/'+ assetObj['asset_group_id'] +'/edit/'+res.data.asset_unique_id]);

      } else {
        if (res['data'] == 'logout') {
          this.loginService.logout();
        }
        this.toaster.showError(res['error']);
      }
    });

  }


  saveGridAssets(assetObj) {
    return this.httpService.PostRequest(api.saveAsset, assetObj).pipe(
      data => data,
      catchError(error => of(error))
    );
  }

  gridEditSaveAsset(assetObj, id) {
    return this.httpService.PutRequest(api.editSaveAsset + id, assetObj).pipe(
      data => data,
      catchError(error => of(error))
    );
  }

  gridDeleteAsset(agId, id) {
    return this.httpService.DeleteRequest(api.deleteAsset + id + '/' + agId).pipe(
      data => data,
      catchError(error => of(error))
    );
  }

  getAllAssets() {
     this.httpService.GetRequest(api.allAsset)
      .subscribe(res => {
        // console.log(res, 'repsponse on getting all the accounts')
        if (res['success']) {
          this.allassets$.next(res['data']);
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }

        }
      });
  }
  getAllAssetsByGroup(id) {
    
      this.httpService.GetRequest(api.getSpecificAssetGroup + id)
        .subscribe(res => {
          // console.log(res, 'accounts by group');
          if (res['success']) {
            this.assets$.next(res['data']);
          } else {
            if (res['data'] == 'logout') {
              this.loginService.logout();
            }
          }
        }, err => {
          const errObj = JSON.parse(err._body);
          if (errObj.success == false) {
            this.assets$.next([]);
          }
        });
      
  }




  uploadRetryCount = 0;
  // mouli
  addAssetDocuments(f: File, assetId: any, success, failure) {
    console.log('addAssetDocuments: ' + f);
    let input = new FormData();
    input.append('file', f, f.name);
    input.append('asset_id', assetId);

    this.httpService.PostRequestForUploads(api.addAssetDocument, input).subscribe(res => {
      this.uploadRetryCount = 0;
      if (res['success']) {
        console.log("res['data']", res['data']);
        this.getAllAssetDocuments(assetId);   // refresh the list of docs
        if (res.data.changedRows == 1) {
          this.toaster.showSuccess('Document successfully updated');
        }
        else {
          this.toaster.showSuccess('Document successfully added');
        }
        success();
      } else {
        if (res['data'] == 'logout') {
          this.loginService.logout();
        }
        else {
          this.toaster.showError(res['error']);
          failure();
        }
      }
    }, err => {
      console.log(err);

      if (JSON.parse(err._body).data == 'please retry') {
        if (this.uploadRetryCount < 2) {
          this.toaster.showError('Unable to add the document! Retrying...');
          this.addAssetDocuments(f, assetId, success, failure)
          this.uploadRetryCount++
        }
        else {
          this.toaster.showError('Unable to add the document! ');
          this.uploadRetryCount = 0;
          failure();
        }
      }

    });
  }

  // mouli
  deleteAssetDocument(docId, assetId) {
    this.httpService.DeleteRequest(api.deleteAssetDocument + docId)
      .subscribe(res => {
        if (res['success']) {
          console.log("res['data']", res['data']);
          this.getAllAssetDocuments(assetId);   // refresh the list of docs
          this.toaster.showSuccess('Document successfully deleted');
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
          this.toaster.showError(res['error']['error']);
        }
      }, err => {
        this.toaster.showError(JSON.parse(err._body).error);

      });
  }

  // mouli
  moveAssetDocument(payload: any, editAssetId: any) {
    this.httpService.PostRequest(api.moveAssetDoc, payload)
      .subscribe(res => {
        console.log(res);
        if (res['success']) {
          console.log("res['data']", res['data']);
          this.getAllAssetDocuments(editAssetId);   // refresh the list of docs
          this.toaster.showSuccess('Document successfully moved');
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
          this.toaster.showError(res['error']['error']);
        }
      }, err => {
        this.toaster.showError(JSON.parse(err._body).error);
      });
  }

  getAllAssetDocuments(assetId) {
    this.httpService.GetRequest(api.getAssetDocuments + assetId)
      .subscribe(res => {
        if (res['success']) {
          this.assetDocs$.next(res['data']);
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
        }
      }, err => {
        const errObj = JSON.parse(err._body);
        if (errObj.success == false) {
          this.assetDocs$.next([]);
        }
      });
  }


  editSaveAsset(assetObj, id, index) {
    this.httpService.PutRequest(api.editSaveAsset + id, assetObj)
      .subscribe(res => {
        console.log(res);
        if (res['success']) {
          this.getAllAssetsByGroup(assetObj['asset_group_id']);

          // const assets = this.assets$.getValue();
          // assets.asset[index] = res['data'];
          // this.assets$.next(assets);

          this.toaster.showSuccess('Account successfully updated');

        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
          this.toaster.showError('Something went wrong');
        }
      }, err => {
        this.toaster.showError('Something went wrong');
        console.error('err', err);
      });
  }

  convertAsset(assetDetails, onSuccess, onFailure) {
    this.httpService.PostRequest(api.ultrasecure, assetDetails)
      .subscribe(res => {
        console.log(res);
        onSuccess();
      }, err => {
        this.toaster.showError(JSON.parse(err._body).error);
        onFailure();
      });
  }

  deleteAsset(agId, i, id) {
    this.httpService.DeleteRequest(api.deleteAsset + id + '/' + agId)
      .subscribe(res => {
        if (res['success']) {
          // const assets = this.assets$.getValue();
          // assets.asset.splice(i, 1);

          // this.assets$.next(assets);
          this.getAllAssetsByGroup(agId);
          this.toaster.showSuccess('Account successfully deleted');

        } else if (!res['success']) {

          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
          this.toaster.showError('Something went wrong');

        }
      }, err => {
        this.toaster.showError(JSON.parse(err._body).error);

      });
  }

}
