import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { AuthService } from 'src/app/services/auth.service';
import { Inject, Component } from '@angular/core';
import { Subscription } from 'rxjs';
import { AssetService } from 'src/app/services/assetService/asset.service';

@Component({
    selector: 'remove-asset-group',
    templateUrl: 'remove-asset-group.html',
})
export class RemoveAssetGroup {
    subscriptions: Subscription[] = [];
    selectedImg = '';
    selectedImgEdit = '';
    isClicked = false;
    groupName = '';
    assetGroup: any;
    deleteId: any;
    errorMsg = '';
    groupNameEdit = '';
    constructor(private asseService: AssetService,
        public dialogRef: MatDialogRef<RemoveAssetGroup>,
        @Inject(MAT_DIALOG_DATA) public data: any) {
            this.assetGroup = data.assetGroup;
        // this.asseService.getAssetGroup();
        // this.subscriptions[0] = this.asseService.assetGroup$.subscribe(data => {
        //     if (data && data.length > 0) {
        //         this.assetGroup = data;
        //     }
        // });
    }

    onNoClick(): void {
        this.dialogRef.close();
    }
    selectImg(name) {
        this.selectedImg = '../assets/images/' + name;
        this.selectedImgEdit = '../assets/images/' + name;
    }

    removeImg() {
        this.selectedImg = '';
        this.selectedImgEdit = '';
    }
    delete() {
        let id = this.assetGroup[this.data.deleteId].ag_id;
        this.asseService.deleteAssetGroup(id, this.data);
        this.dialogRef.close();
    }
    closedialog() {
        this.dialogRef.close();
    }

}