import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { AuthService } from 'src/app/services/auth.service';
import { Inject, Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { AssetService } from 'src/app/services/assetService/asset.service';

@Component({
  selector: 'add-asset-group',
  templateUrl: 'add-asset-group.html',
})
export class AddAssetGroup implements OnInit {
  selectedImg = '';
  subscriptions: Subscription[] = [];
  selectedImgEdit = '';
  isClicked = false;
  groupName = '';
  assetGroup: any;
  deleteId: any;
  buttonText: any = "Create";
  groupTitle:any="Create New Drawer ";
  errorMsg = '';
  successMsg=''; 
  groupNameEdit = '';
  numberOfAssets;
  
  constructor( 
    private assetService : AssetService,
    public dialogRef: MatDialogRef<AddAssetGroup>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
    if (data != null) {
      this.selectedImg = this.data.selectedImgEdit;
      this.groupName = this.data.groupNameEdit;
      this.numberOfAssets = this.data.numberOfAssets;
      this.assetGroup=this.data.assetGroup;
      this.groupTitle="Edit Drawer";
      this.buttonText = "Save";
    }

  }
  ngOnInit() {
  this.getData();
  }
  getData() {
    this.assetService.getAssetGroup();
    this.subscriptions[0] = this.assetService.assetGroup$.subscribe(data => {
      if (data && data.length > 0) {
        // this.assetGroup = [];
        this.assetGroup = data;
        console.log(this.assetGroup);
        // this.cd.detectChanges();
      }
    });
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
  selectImg(name) {
    this.selectedImg = '../assets/images/' + name;
    this.selectedImgEdit = '../assets/images/' + name;
  }

  removeImg() {
    this.selectedImg = '';
    this.selectedImgEdit = '';
  }

  saveAssetGroup() {
    if (this.isClicked) {
      return false;
    }
    this.errorMsg = '';
    if (this.groupName.trim() === '') {
      this.errorMsg = 'Please Enter Group Name';
      return false;
    }
    if (this.groupName.length > 30) {
      this.errorMsg = 'Group Name must be less than 30 chars';
      return false;
    }

    if (this.selectedImg === '') {
      this.errorMsg = 'Please Select Group Icon';
      return false;
    }
    let add = false;
    if (this.groupName != '') {
      if (typeof this.assetGroup !== 'undefined') {
        this.assetGroup.forEach(element => {
          if (element.asset_group_title.toLowerCase() === this.groupName.trim().toLowerCase()) {
            this.errorMsg = 'Duplicate Name for Account Group';
            return false;
            add=true
          }
        });

      }
    }
    const assetGroupObj = {
      user_asset_group_title: this.groupName.trim(),
      user_asset_group_icon: this.selectedImg,
      Number_of_assets: this.numberOfAssets
    }
    if (this.data != null) {
      this.assetService.editSaveAssetGroup(assetGroupObj, this.data.editId, this.data.editIndex);
    } else {
      this.assetService.saveAssetGroup(assetGroupObj);
    }
    this.dialogRef.close();

  }

  closedialog() {
    this.dialogRef.close();
  }

}