  sortAssetsBy(property, dir) {
    this.assets.sort(function (a, b) {
      if (typeof a[property] === 'string' && property !== 'asset_security_mode') {

        const nameA = a[property].toLowerCase(), nameB = b[property].toLowerCase()
        
        if (nameA < nameB) //sort string ascending
          return dir > 0 ? +1 : -1
        if (nameA > nameB)
          return dir > 0 ? -1 : +1
        return 0 //default return value (no sorting)
      }else if (property === 'asset_security_mode'){
         if (a.asset_security_mode === "ULTSEC") //sort string ascending
          return dir > 0 ? +1 : -1
         if (b.asset_security_mode === "ULTSEC")
          return dir > 0 ? -1 : +1
         return 0 
      } else {
        return dir > 0 ? a[property] - b[property] : b[property] - a[property]
      }
    })


  }

