import { IContacts } from './';
export class BeneficiariesClass {


  private beneficiaries: IContacts[] = [];
  valueChanged: (newVal, id?) => void

  constructor(
    valueChanged?: (newVal, id?) => void
  ) {
    this.valueChanged = valueChanged || (() => {});
  }

  /**
   * set beneficiaries list
   */
  public setBeneficiaries(beneficiaries: IContacts[]): void {
    this.beneficiaries = beneficiaries;
  }

  /**
   * get beneficiaries list
   */
  public getBeneficiaries(): IContacts[] {
    return this.beneficiaries;
  }

  /**
   * set single beneficiary
   */
  public setBeneficiary(beneficiary: IContacts): void {
    const currentList = [...this.beneficiaries];
    currentList.push(beneficiary);

    this.beneficiaries = currentList;
  }

  /**
   * set single beneficiary
   */
  public removeBeneficiary(caretakerId: number): void {
    const currentList = this.beneficiaries.filter(item => item.user_caretaker_id !== caretakerId);
    this.beneficiaries = currentList;
  }

  /**
   * is beneficiaries list empty
   */
  public isEmpty(): boolean {
    return this.beneficiaries.length === 0;
  }

  /**
   * is beneficiaries list full
   */
  public isFull(): boolean {
    return this.beneficiaries.length >= 5;
  }


  public clearAll(): void {
    this.beneficiaries = [];
  }
}
