import { Injectable } from '@angular/core';
import { api } from '../api';
import { Observable } from 'rxjs'
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { HttpService } from '../http.service';
import { HttpClient } from '@angular/common/http';
import { from } from 'rxjs';
import { ToasterService } from '../toaster.service';
import { Idle } from '@ng-idle/core';
import { Router } from '@angular/router';
import { LoginService } from '../loginService/login.service';
// import { HttpService } from './http.service';


@Injectable()
export class HelpService {
    assetGroup$: BehaviorSubject<any> = new BehaviorSubject([]);
    base = 'https://dev.clocr.com';
    sample: any = "assas";
    //sidnav : any = false;
    public sidnav: any = false;
    user$: BehaviorSubject<any> = new BehaviorSubject(undefined);
    error$: BehaviorSubject<any> = new BehaviorSubject(undefined);
    startupHelpEnabled = "YES";
    helpvideos$: BehaviorSubject<any> = new BehaviorSubject([]);


    constructor(private httpService: HttpService,
        private idle: Idle,
        private router: Router,
        private loginService: LoginService,
        private toaster: ToasterService) { }


    gethelpvidsdata() {
        this.httpService.GetRequest(api.helpVideos).subscribe(res => {
            // console.log('JSON data-> ' + JSON.stringify(res));
            if (res['success']) {
                console.log(JSON.stringify(res['data']['videoData'][0].title));
                this.helpvideos$.next(res['data']['videoData']);
            }
        },
            err => {
                this.toaster.showError('Something went wrong');
            });
    }

    startupHelp(value, id) {

        if (value == true)
            this.startupHelpEnabled = "YES";
        else
            this.startupHelpEnabled = "NO";

        this.httpService.PutRequest(api.helpOnStartup + id + '/update/profile/startuphelpenabled/', { "value": this.startupHelpEnabled })
            .subscribe(res => {

                if (res['success']) {
                    if (this.startupHelpEnabled == "YES") {
                        this.toaster.showSuccess('Next time you login you will be taken to "Help Videos".');
                    }
                    else {
                        this.toaster.showSuccess('Next time you login you will be taken to "Dashboard".');
                    }


                } else if (!res['success']) {
                    if (res['data'] == 'logout') {
                        this.loginService.logout();
                    }
                }
            }, err => {
                this.toaster.showError('Something went wrong');

            });
    }


}
