import { Directive,ElementRef, Renderer2, HostListener, Input } from '@angular/core';

@Directive({
  selector: '[clocrNumericOnly]'
})
export class NumericOnlyDirective {

  constructor(private elem: ElementRef,private renderer: Renderer2) { 
    
  }

  @Input('clocrNumericOnly') oldValue: any;
  olderValue: any = "";
  @HostListener('input') keypressed(){
    var reg = /^(\s*|\d+)$/;
    var eleValue = this.elem.nativeElement.value;
    if(!reg.test(eleValue)){
      this.renderer.setProperty(this.elem.nativeElement,'value', this.olderValue)
    }else{
      this.renderer.setProperty(this.elem.nativeElement,'value', eleValue);
      this.olderValue = eleValue;
    }
  }

}
