import { Pipe, PipeTransform } from "@angular/core";
@Pipe({ name: "OrderBy" })
export class Order implements PipeTransform {
  transform(items: Array<any>, order: any): any {
    if (order == null) {
      return items;
    }

    return items.sort((a: any, b: any) => {
      if (a[order] > b[order]) {
        return 1;
      } else if (a[order] < b[order]) {
        return -1;
      } else {
        return 0;
      }
    });
  }
}
