import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { api } from '../api';
import { Observable } from 'rxjs'
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { HttpService } from '../http.service';
import { HttpClient } from '@angular/common/http';
import { from, of } from 'rxjs';
import { LoginService } from '../loginService/login.service';
import { ToasterService } from '../toaster.service';
import { Router } from '@angular/router';
import { catchError } from "rxjs/operators";

// import { HttpService } from './http.service';


@Injectable()
export class PasswordHintService {
  passwordHint$: BehaviorSubject<any> = new BehaviorSubject([]);
  assetGroup$: BehaviorSubject<any> = new BehaviorSubject([]);
  base = 'https://dev.clocr.com';
  sample: any = "assas";
  //sidnav : any = false;
  public sidnav: any = false;
  getPasswordHintReport$: BehaviorSubject<any> = new BehaviorSubject([]);


  constructor(private httpService: HttpService,
    private toaster: ToasterService,
    private loginService: LoginService,
    private router: Router) { }


  getPasswordHintReport() {
    this.httpService.PostRequest(api.getPasswordHintReport, { 'test': 'data' })
      .subscribe(res => {
        if (res['success']) {
          const getPasswordHintReport = this.getPasswordHintReport$.getValue();
          getPasswordHintReport.push(res['data']);
          this.getPasswordHintReport$.next(res['data']);

        } else if (!res['success']) {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
        }
      }, err => {
        this.toaster.showError('Something went wrong');

      });
  }

  getPasswordHint() {
    this.httpService.GetRequest(api.getPasswordHint)
      .subscribe(res => {
        if (res['success']) {
          console.log("Password service data ", res['success']);
          this.passwordHint$.next(res['data']);
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }

        }
      });
  }

  getPasswordHints() {
    return this.httpService.GetRequest(api.getPasswordHint).pipe(
      map(res => {
        if (!res['success']) {
          this.loginService.logout();
          return;
        }
        return res;
      }))
  }

  getAllPasswordHint() {
    return this.httpService.GetRequest(api.getPasswordHint).pipe(
      data => data,
      catchError(error => of(error))
    );
  }



  savePasswordHint(pwdObj: object) {
    this.httpService.PostRequest(api.savePasswordHint, pwdObj).subscribe(res => {
      if (res['success']) {
        const passwordHint = this.passwordHint$.getValue();
        passwordHint.push(res['data']);
        this.passwordHint$.next(passwordHint);
        this.toaster.showSuccess('Password hint successfully added');
      } else {
        if (res['data'] == 'logout') {
          this.loginService.logout();
        }
        this.toaster.showError(res['error']);
      }
    });
  }
  editSavePasswordHint(pwdObj, id, index) {
    this.httpService.PutRequest(api.editSavePasswordHint + id, pwdObj)
      .subscribe(res => {
        if (res['success']) {
          const passwordHint = this.passwordHint$.getValue();
          passwordHint[index] = res['data'];
          this.passwordHint$.next(passwordHint);
          let currentUrl = this.router.url;
          this.router.navigateByUrl('/', { skipLocationChange: true }).then(() => {
            this.router.navigate([currentUrl]);
          });
          this.toaster.showSuccess('Password hint successfully updated');
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
          this.toaster.showError(res['error']);
        }
      }, err => {
        this.toaster.showError('Something went wrong');
        console.error('err', err);
      });
  }
  deletePasswordHint(id, i) {
    this.httpService.DeleteRequest(api.deletePasswordHint + id)
      .subscribe(res => {
        if (res['success']) {
          const passwordHint = this.passwordHint$.getValue();
          passwordHint.splice(i, 1);

          this.passwordHint$.next(passwordHint);
          this.toaster.showSuccess('Password hint successfully deleted');

        } else if (!res['success']) {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }

          this.toaster.showError('Something went wrong');

        }
      }, err => {
        this.toaster.showError('Something went wrong');

      });
  }
}