import { Injectable } from '@angular/core';
import { api } from '../api';
import {Observable} from 'rxjs'
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { HttpService } from '../http.service';
import { HttpClient } from '@angular/common/http';
import { from } from 'rxjs';
import { Router } from '@angular/router';
import { LoginService } from '../loginService/login.service';
import { LegalService } from '../legalService/legal.service';
import { ToasterService } from '../toaster.service';
// import { HttpService } from './http.service';


@Injectable()
export class BeneficiaryService {
  assetGroup$: BehaviorSubject<any> = new BehaviorSubject([]);
  beneficiary$: BehaviorSubject<any> = new BehaviorSubject([]);

   base = 'https://dev.clocr.com';
   sample : any = "assas";
   //sidnav : any = false;
  public sidnav: any = false;

  constructor(private router: Router,
    private httpService: HttpService, 
    private loginService: LoginService,
    private legalService : LegalService,
    private toaster: ToasterService) { }
    

  getBeneficiaries() {
    this.httpService.GetRequest(api.getallbeneficiaries)
      .subscribe(res => {
        if (res['success']) {
          this.beneficiary$.next(res['data']);
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }

        }
      });
  }

  // added for editing the beneficiary
  editSavebeneficiary(beneficiaryObj, id, index) {
    this.httpService.PutRequest(api.editbeneficiary + id, beneficiaryObj)
      .subscribe(res => {
        if (res['success']) {
          // const beneficiary = this.beneficiary$.getValue();
          // beneficiary[index] = res['data'];
          // this.beneficiary$.next(beneficiary);
          this.getBeneficiaries();
          this.toaster.showSuccess('Beneficiary successfully updated');

        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }

          this.toaster.showError('Something went wrong');
        }
      }, err => {
        this.toaster.showError('Something went wrong');
        console.error('err', err);
      });
  }

  //added for updating the drag change
  shuffleBeneficiaries(shuffleList, onSuccess, onFailure) {
    this.httpService.PostRequest(api.shuffleBeneficiaries, shuffleList)
      .subscribe(res => {
        if (res['success']) {
          onSuccess();
          this.toaster.showSuccess('Priority successfully changed');
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
          onFailure();
          this.toaster.showError('Something went wrong, priority is not updated');
          console.log(res);
        }
      }, err => {
        onFailure();
        this.toaster.showError(JSON.parse(err._body).error + ', priority is not updated');
        console.error('err', err);
      });
  }

   // this is an added auth service for beneficiary
   saveBeneficiary(beneficiaryObj: object) {
    this.httpService.PutRequest(api.savebeneficiary, beneficiaryObj).subscribe(res => {
      if (res['success']) {
        // const beneficiary = this.beneficiary$.getValue();
        // beneficiary.push(res['data']);
        // this.beneficiary$.next(res['data']);

        this.getBeneficiaries();
        this.toaster.showSuccess('beneficiary successfully added');

      } else {
        if (res['data'] == 'logout') {
          this.loginService.logout();
        }
        this.toaster.showError(res['error']);
      }
    });

  }

  
  // added for beneficiary
  deletebeneficiary(id, i) {
    this.httpService.DeleteRequest(api.deletebeneficiary + id)
      .subscribe(res => {
        if (res['success']) {
          // const beneficiary = this.beneficiary$.getValue();
          // beneficiary.splice(i, 1);

          // this.beneficiary$.next(beneficiary);
          this.getBeneficiaries();
          this.toaster.showSuccess('Beneficiary successfully removed.');

        } else if (!res['success']) {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }

        }
      }, err => {
      });
  }


  sendMsgToBeneficiary(email, msg, subject) {
    this.httpService.PostRequest(api.msgTosaveCaretaker, { email: email, msg: msg, subject: subject })
      .subscribe(res => {
        if (res['success']) {
          this.toaster.showSuccess('Email successfully sent to Beneficiary');

        } else if (!res['success']) {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }
        }
      }, err => {
        this.toaster.showError('Something went wrong');

      });
  }

}
