import 'rxjs/add/operator/map';

import { Injectable } from '@angular/core';
import { Headers, Http, RequestOptions, Response, ResponseContentType } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Router } from '@angular/router';


interface Test {
  success: boolean;
  data: any;
  error: any;
}

export interface CustomServerResponseObject { success: boolean; data: any; error: any; }

@Injectable()
export class HttpService {

  constructor(private _http: Http, private router: Router) { }

  GetHeaders() {
    const headers: Headers = new Headers();
    headers.append('Content-Type', 'application/json');
    if (localStorage.getItem('token')) {
      const token = localStorage.getItem('token');
      headers.append('x-access-token', token);
    }
    const options: RequestOptions = new RequestOptions();
    options.headers = headers;
    return options;
  }

  GetHeadersForFormData() {
    const headers: Headers = new Headers();
    // headers.append('Content-Type', 'application/formdata');
    if (localStorage.getItem('token')) {
      const token = localStorage.getItem('token');
      headers.append('x-access-token', token);
    }
    const options: RequestOptions = new RequestOptions();
    options.headers = headers;
    return options;
  }

  GetHeadersForDownloadFile() {
    const headers: Headers = new Headers();
    if (localStorage.getItem('token')) {
      const token = localStorage.getItem('token');
      headers.append('x-access-token', token);
    }
    const options: RequestOptions = new RequestOptions({responseType: ResponseContentType.Blob });
    options.headers = headers;
    return options;
  }

  ResponseMap(res: Response) {
    const response: CustomServerResponseObject = res.json();
    if (response && response.hasOwnProperty('success')) {
      if (response.error && response.error === 'Failed to authenticate token.') {
        localStorage.removeItem('token');
        // this.router.navigate(['/logout']);
        window.location.reload();
      }
      return response;
    } else {
      return {
        success: false,
        data: null,
        error: response
      };
    }
  }
  GetRequest(url: string): Observable<CustomServerResponseObject> {
    return this._http.get(url, this.GetHeaders()).map(this.ResponseMap);
  }
  PostRequest(url: string, obj: Object): Observable<CustomServerResponseObject> {
    return this._http.post(url, obj, this.GetHeaders()).map(this.ResponseMap);
  }
  PutRequest(url: string, obj: Object): Observable<CustomServerResponseObject> {
    return this._http.put(url, obj, this.GetHeaders()).map(this.ResponseMap);
  }
  DeleteRequest(url: string): Observable<CustomServerResponseObject> {
    return this._http.delete(url, this.GetHeaders()).map(this.ResponseMap);
  }
  PostRequestForUploads(url: string, obj: any): Observable<CustomServerResponseObject> {
    return this._http.post(url, obj, this.GetHeadersForFormData()).map(this.ResponseMap);
  }
  GetRequestForDownload(url:string): Observable<Blob> {
    return this._http.get(url, this.GetHeadersForDownloadFile())
    .map(res => res.blob()) 
  }

  // validateToken(response) {
  //   response.subscribe((res) => {
  //     if (!res.success) {
  //       if (res.error === 'Failed to authenticate token.') {
  //         this.router.navigate(['/logout']);
  //       }
  //     }
  //   });
  // }
}
