import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { HttpService } from "../services/http.service";
import { LoginService } from "../services/loginService/login.service";
import { AuthService } from "../services/auth.service";
import { Observable } from 'rxjs/Observable';

import {Subject} from "rxjs/Rx";
@Injectable()
export class OnboardGuard implements CanActivate {
  
  userStatus: any;
  constructor(private router: Router, private httpService: HttpService, private loginService: LoginService, private authService: AuthService) { }

   canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | boolean {
    if (localStorage.getItem('token')) {

          if(sessionStorage.getItem("clocr_user")===null){

              var onboareded = this.authService.getUserDetailsForGuard();
              var subject = new Subject<boolean>();
              onboareded.subscribe(
                  (res) => {
                    console.log("onNext guard: "+ res);
                    subject.next(res);
                  });
              return subject.asObservable().first();

            } else {
                var user = JSON.parse(sessionStorage.getItem("clocr_user"));
                if(user.onboard_complete == "NO"){
                  console.log('onboarding not yet completed');
                  return true;
                } else {
                  this.router.navigate(['/dashboard']);
                  return false;
                }    
            }
    }
  }

}