
import { IAccounts } from './IAccounts';

interface IEstatePlanData {
  lastUpdate: string;
  isComplete: boolean;
}

export class AccountsClass {


  private estatePlanData: IEstatePlanData = {
    isComplete: null,
    lastUpdate: null
  };
  private accounts: IAccounts[] = [];
  private checkedAccounts: IAccounts[] = [];
  valueChanged: (newVal, id?) => void

  constructor(
    valueChanged?: (newVal, id?) => void
  ) {
    this.valueChanged = valueChanged || (() => {});
  }

  /**
   * get accounts list
   */
  public getAccounts(): IAccounts[] {
    return this.accounts;
  }

    /**
   * set single account
   */
  public setAccount(account: IAccounts): void {
    const currentList = [...this.accounts];
    currentList.push(account);
    this.accounts = currentList;
  }

  /**
   * set accounts
   */
  public setAccounts(accounts: IAccounts[]): void {
    this.accounts = accounts;
  }

  /**
   * set new array of accounts
   */
  public concatAccounts(accounts: IAccounts[]): void {
    
    /**
     * check if estate plan is complete
     */
    accounts.forEach(item => {
      if (item.beneficiaries && item.beneficiaries.length === 0) {
        this.estatePlanData.isComplete = false;
        return;
      }
      this.estatePlanData.isComplete = true;
    })
      
    const localAccounts = this.accounts
    this.accounts.push.apply(localAccounts, accounts);
  }

  /**
   * remove account from list
   */
  public removeAccount(accountId: number): void {
    const accounts = this.accounts.filter(account => account.asset_unique_id !== accountId);
    this.accounts = accounts;
  }


   /**
   * get accounts list
   */
  public getCheckedAccounts(): IAccounts[] {
    return this.checkedAccounts;
  }

  /**
   * set accounts
   */
  public setCheckedAccounts(checkedAccounts: IAccounts[]): void {
    this.checkedAccounts = checkedAccounts;
  }

  /**
   * set checked account
   */
  public setCheckedAccount(checkedAccount: IAccounts): void {
    let checkedAccounts = this.checkedAccounts;
    checkedAccounts.push(checkedAccount)
    
    this.checkedAccounts = checkedAccounts;
  }

  /**
   * remove checked account from list
   */
  public removeCheckedAccount(checkedAccountId: number): void {
    const checkedAccounts = this.checkedAccounts.filter(account => account.asset_unique_id !== checkedAccountId);

    this.checkedAccounts = checkedAccounts;
  }

  /**
   * is account list is empty
   */
  public isEmpty(): boolean {
    return this.accounts.length === 0;
  }

  public isCheckedEmpty(): boolean {
    return this.checkedAccounts.length === 0;
  }

  public clearAll(): void {
    this.accounts = [];
    this.checkedAccounts = [];
  }

  public setEstatePlanData(isComplete?: boolean, lastUpdate?: string): any {
    if (isComplete !== undefined) this.estatePlanData.isComplete = isComplete;
    if (lastUpdate !== undefined) this.estatePlanData.lastUpdate = lastUpdate;
  }

  public getEstatePlanData(): IEstatePlanData {
    return this.estatePlanData;
  }

}
