  updateAccountsList() {
    this.assetsByAssetGroupId = [];
    this.allAssets.forEach(element => {
      if (element.asset_group_id == this.selectedAssetGroupId && element.asset_security_mode == this.selectedAssetToBeMoved.file_security_mode) {
        this.assetsByAssetGroupId.push(element);
        this.remainingAccounts = [];
        if (this.selectedAsset) {
          for (var grp of this.assetsByAssetGroupId) {
            if (this.selectedAsset.asset_unique_id != grp.asset_unique_id) {
              this.remainingAccounts.push(grp);
            }
          }
        }
      }
      return;
    });
  }

