  addAssetDocument() {
    this.errorMsg1 = '';

    var filesSupported = this.fileRestrictions.uploadfiles_exts;
    let exts = filesSupported.split(',');
    exts = exts.map((ext: string) => {
      const e = ext.trim();
      return e;
    });

    //var regEx = new RegExp(filesSupported.split(',').join('|\.'), 'i')
    var regEx = new RegExp(exts.join('|\.'), 'i')
    var fileSizeSupported = this.fileRestrictions.uploadfiles_size;

    // const fileName = this.file.name.substr(0, this.file.name.lastIndexOf('.'));
    // let extName = this.file.name.split('.').pop();
    // if('JPG' === extName.toUpperCase()){
    //   extName = extName.toUpperCase();
    // }else{
    //   extName = extName.toLowerCase();
    // }

    if (!this.file.name.toLowerCase().match(regEx)) { // validation of file extension using regular expression before file upload
      //if (!extName.match(regEx)){    
      this.errorMsg1 = 'Invalid file format';
      return false;
    }

    else {

      if (this.file.size > (fileSizeSupported * 1024 * 1024)) {

        this.errorMsg1 = 'File Size is greater than ' + fileSizeSupported + 'MB.';
        return false;
      }
      else {
        this.saveStarted = true;
        this.assetService.addAssetDocuments(this.file, this.editId, // TODO
          () => {
            this.saveStarted = false;
            this.uploadDocs = '';
            $('#19-01').modal('hide');
          },
          () => {
            this.saveStarted = false;
            $('#19-01').modal('hide');
          }
        );


      }
    }
  }

