  editAccess(id, value, i) {

    if (value == 'asset_no_access') {
      this.as.editCaretakerPermissions(id, value);
      this.as.editCaretakerPermissionsUnchecked(id, 1);

      //$('.radio'+i).attr('checked',false);
      //$('.radio'+i).prop('checked',false);


      $('input[name="service3-' + i + '"]').prop('checked', false);
      $('input[name="service2-' + i + '"]').prop('checked', false);
      $('input[name="service1-' + i + '"]').prop('checked', false);


    } else {
      // $('.radio'+i).attr('checked',false);
      // $('.radio'+i).prop('checked',false);

      $('input[name="service0-' + i + '"]').prop('checked', false);

      this.as.editCaretakerPermissions(id, value);
      this.as.editCaretakerPermissionsUnchecked(id, 2);

    }
  }


  ngOnDestroy() {
    this.subscriptions.forEach((subscription: Subscription) => {
      subscription.unsubscribe();
    });
  }
  closeAssetUpload() {
    $('#19-01').modal('hide');
  }

