import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-gridview',
  templateUrl: './gridview.component.html',
  styleUrls: ['./gridview.component.scss']
})
export class GridviewComponent implements OnInit {
  @Input() schema: Array<any>
  @Input() data: Array<any>
  @Output() saved = new EventEmitter<Object>();
  @Output() onUpdate = new EventEmitter<Object>();
  @Output() onDelete = new EventEmitter<number>();

  intermediateState: Object = {}

  selectedIndex: Number = -1

  addedRows: Array<any> = []
  isNew: Boolean = false

  constructor() { }

  ngOnInit() {
    console.log(this.schema, this.data);
  }

  onEdit(i: string) {
    this.intermediateState = Object.assign({}, this.data[i]);
    this.selectedIndex = parseInt(i)
  }

  onCancel() {
    this.intermediateState = {}
    this.selectedIndex = -1
  }

  onSave() {
    if (this.isNew) {
      this.addedRows = []
      this.isNew = false;
      this.saved.emit({ selectedIndex: this.selectedIndex, data: this.intermediateState })
    }else{
      this.onUpdate.emit({ selectedIndex: this.selectedIndex, data: this.intermediateState })

    }
    this.selectedIndex = -1
  }

  customTrackBy(index: number, obj: any): any {
    // console.log(index)
    return index;
  }

  public onAddRow() {
    this.intermediateState = {}
    this.selectedIndex = this.data.length;
    this.addedRows = [this.intermediateState]
    this.isNew = true;
  }

  onDeleteAsset(index: number) {
    this.onDelete.emit(index)
  }
}
