import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

// services
import { HttpService } from './../../services/http.service';
import { api } from './../../services/api';

// class/models
import { BeneficiariesClass } from './../../core/models/BeneficiariesClass';

@Injectable()
export class BeneficiariesService {

  public beneficiaries = new BeneficiariesClass();
  public accounts = new BeneficiariesClass();

  constructor(
    private httpService: HttpService
  ) { }

  public updateBeneficiariesByAccount(accountId: number, beneficiaries): Observable<any> {
    return this.httpService.PostRequest(api.saveBeneficiariesForAccount + accountId, beneficiaries);
  }

  public getBeneficiariesByAccount(accountId: number): Observable<any> {
    return this.httpService.GetRequest(api.getBeneficiariesForAccount + accountId);
  }

  public getAllBeneficiaries(): Observable<any> {
    return this.httpService.GetRequest(api.getAllBeneficiaries);
  }

  public getBulkBeneficiariesByAccounts(accountsBeneficiaries): Observable<any> {
    return this.httpService.PostRequest(api.saveBulkBeneficiariesForAccounts, accountsBeneficiaries);
  }

}
