import { Injectable } from "@angular/core";
import { api } from "../api";
import { Observable } from "rxjs";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { HttpService } from "../http.service";
import { HttpClient } from "@angular/common/http";
import { from } from "rxjs";
import { ToasterService } from "../toaster.service";
import { Idle } from "@ng-idle/core";
import { Router } from "@angular/router";
// import { UnverifiedUserComponent } from "src/app/components/unverified-user/unverified-user.component";
import { MatDialog } from "@angular/material";
// import { HttpService } from './http.service';

@Injectable()
export class LoginService {
  assetGroup$: BehaviorSubject<any> = new BehaviorSubject([]);
  base = "https://dev.clocr.com";
  sample: any = "assas";
  //sidnav : any = false;
  public sidnav: any = false;
  token: string = this.getToken();
  registrationStatus$: BehaviorSubject<any> = new BehaviorSubject(undefined);
  user$: BehaviorSubject<any> = new BehaviorSubject(undefined);
  error$: BehaviorSubject<any> = new BehaviorSubject(undefined);
  startupHelpEnabled = "YES";

  constructor(
    private httpService: HttpService,
    private idle: Idle,
    public dialog: MatDialog,
    private router: Router,
    private toaster: ToasterService
  ) {}

  logout() {
    return this.httpService
      .PostRequest(api.removeToken, { token: this.token })
      .subscribe(res => {
        this.idle.stop();
        localStorage.removeItem("token");
        sessionStorage.removeItem("clocr_user");
        this.user$.next(undefined);
        this.router.navigate(["/login"]);
      });
  }

  registerVerify(user: object) {
    return this.httpService.PostRequest(api.registerVerify, user);
  }

  registration(user): any {
    var mail = user.email;
    //return this.httpService.PutRequest(api.registration, user);
    this.httpService.PutRequest(api.registration, user).subscribe(res => {
      if (res["success"]) {
        this.setToken(res["data"]["authToken"]);
        const user = res["data"]["user"];
        localStorage.removeItem('registration_user');
        localStorage.setItem("promo", JSON.stringify(res.data.promocode));
        this.user$.next(user);
        this.router.navigate(["/onBoarding"]);
      } else {
        console.log(res["error"]);
        

        if(res["error"] == "Sorry, this promotion has expired."){
          console.log('expired');
          localStorage.setItem("registration_user", JSON.stringify(user));

          this.registrationStatus$.next(res["error"]);
          setTimeout(() => {
            this.router.navigate(["/signup"]);
            this.registrationStatus$.next(null);
          }, 3000);
        }

        if (res["error"] == "You are already a registered user. Please authenticate your email.") {
          this.toaster.showSuccess(res['error']);
          setTimeout(() => {
            localStorage.removeItem('registration_user');
            this.router.navigate(["/login"]);
          }, 5000);
        }
        if (res["error"] == "You are a registered CLOCR user. Please login.") {
          this.toaster.showSuccess(res['error']);
          setTimeout(() => {
            localStorage.removeItem('registration_user');
            this.router.navigate(["/login"]);
          }, 5000);
        }
        // this.registrationStatus$.next(res["error"]);
      }
    },
    err => {
      const errObj = JSON.parse(err._body);
      if(errObj.error == "Sorry, this promotion has expired."){
        console.log('expired');
        localStorage.setItem("registration_user", JSON.stringify(user));

        this.registrationStatus$.next(errObj.error);
        setTimeout(() => {
          this.router.navigate(["/signup"]);
          this.registrationStatus$.next(null);
        }, 3000);
      }

      // this.registrationStatus$.next(errObj.error);

    });
  }

  login(user: Object) {
    this.httpService.PostRequest(api.signin, user).subscribe(
      res => {
        if (res["success"]) {
          var otp_key = res["data"]["user"]["otp_key"];
          if (otp_key !== undefined) {
            return this.router.navigate([
              "/otp/" + res["data"]["user"].otp_key
            ]);
          } else {
            this.setToken(res["data"]["token"]);
            this.user$.next(res["data"]["user"]);
            console.log(res.data.user)
            if(res.data.user.onboard_complete == "NO"){
              return this.router.navigate(["/onBoarding"]);
            }
            if (res["data"]["user"]["user_tc_accepted"] == "0") {
              return this.router.navigate(["/dashboard"]);
            }
            //  else if (res['data']['user']['user_help_completed'] != '1') {
            // return this.router.navigate(['/signup-details']);
            // }
            else {
              this.httpService.GetRequest(api.getUserDetails).subscribe(res => {

                if (res["success"]) {
                  this.startupHelpEnabled = res["data"]["startuphelpenabled"];
                  if (res["data"]["startuphelpenabled"] == "YES") {
                    // return this.router.navigate(["/helpvideos"]);
                    return this.router.navigate(["/dashboard"]);
                  } else {
                    return this.router.navigate(["/dashboard"]);
                  }
                } else {
                  if (res["data"] == "logout") {
                    this.logout();
                  }
                }
              });
            }
          }
          /*
        this.setToken(res['data']['token']);
        this.user$.next(res['data']['user']);
        if (res['data']['user']['user_tc_accepted'] == '0') {
          return this.router.navigate(['/terms']);
        } else if (res['data']['user']['user_help_completed'] != '1') {
          return this.router.navigate(['/signup-details']);
        } else {
          return this.router.navigate(['/dashboard']);
        }
        */
        } else {
          // this.toaster.showError(res['error']);
          this.error$.next(res["error"]);
        }
      },
      err => {
        const errObj = JSON.parse(err._body);
        this.error$.next(errObj.error);
      }
    );
  }

  otp(user: Object) {
    console.log(api.otp, "api.otp");
    console.log(user, "user");
    this.httpService.PostRequest(api.otp, user).subscribe(
      res => {
        console.log("login call", res);
        if (res["success"]) {
          this.setToken(res["data"]["token"]);
          this.user$.next(res["data"]["user"]);
          localStorage.setItem("sidenav", "true");
          if(res.data.user.onboard_complete == "NO"){
            return this.router.navigate(["/onBoarding"]);
          }
          if (res["data"]["user"]["user_tc_accepted"] == "0") {
            return this.router.navigate(["/dashboard"]);
          }
          //  else if (res['data']['user']['user_help_completed'] != '1') {
          // return this.router.navigate(['/signup-details']);
          // }
          else {
            this.httpService.GetRequest(api.getUserDetails).subscribe(res => {
              if (res["success"]) {
                this.startupHelpEnabled = res["data"]["startuphelpenabled"];
                if (res["data"]["startuphelpenabled"] == "YES") {
                  // return this.router.navigate(['/how-it-works']);
                  // return this.router.navigate(["/helpvideos"]);
                  return this.router.navigate(["/dashboard"]);
                } else {
                  return this.router.navigate(["/dashboard"]);
                }
              } else {
                if (res["data"] == "logout") {
                  this.logout();
                }
              }
            });
          }
        } else {
          this.toaster.showError(res["error"]);
          this.error$.next(res["error"]);
        }
      },
      err => {
        const errObj = JSON.parse(err._body);
        this.toaster.showError(errObj.error);
      }
    );
  }

  newemailVerification(email) {
    return this.httpService.PutRequest(api.emailVerification, email);
  }

  forgotPassword(email) {
    return this.httpService.PostRequest(api.forgotPassword, email);
  }

  verifyToken() {
    return this.httpService
      .PostRequest(api.verifyToken, { token: this.token })
      .map(
        res => {
          if (res.success) {
            this.user$.next(res.data);
            return true;
          } else {
            if (res["data"] == "logout") {
              this.logout();
            }
            this.router.navigate(['/login']);
          }
        },
        err => {
          console.log("err", err);
        }
      );
  }

  resetPassword(body) {
    // api.forgetPassword
    return this.httpService.PostRequest(api.resetPassword, body);
  }

  isLoggedIn(state, checkService = false) {
    const user = this.user$.getValue();
    if (user) {
      if (state.url !== "/login" && state.url !== "/signup") {
        // if (this.getToken() && checkService) {
        //   this.logout();
        //   return this.verifyToken();
        // }
        return Observable.of(true);
      } else {
        if (user["user_tc_accepted"] == "0") {
          this.router.navigate(["/dashboard"]);
        } else if (user["user_help_completed"] != "1") {
          // this.router.navigate(['/signup-details']);
        } else {
          this.router.navigate(["/dashboard"]);
        }
        // this.router.navigate(['/dashboard']);
      }
    } else if (this.getToken()) {
      if (state.url === "/login" || state.url === "/signup") {
        this.router.navigate(["/dashboard"]);
      } else {
        return this.verifyToken();
      }
    } else if (state.url === "/login" || state.url === "/signup") {
      return Observable.of(true);
    } else {
      this.router.navigate(["/login"]);
    }
  }
  getToken() {
    return localStorage.getItem("token")
      ? localStorage.getItem("token")
      : undefined;
  }

  setToken(token: string) {
    localStorage.setItem("token", token);
  }

  removeToken() {
    localStorage.removeItem("token");
  }

  updateProfile(userId, body) {
    return this.httpService.PutRequest(
      api.updateProfile + userId + "/update/profile",
      body
    );
  }
}
