import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { HttpParams } from '@angular/common/http';

// services
import { HttpService } from './../../services/http.service';
import { LoginService } from './../../services/loginService/login.service';
import { api } from './../../services/api';

// models/classes
import { AccountsClass } from '../models/Accounts';

@Injectable()
export class AccountsService {

  public accounts = new AccountsClass();
  public estatePlan = new AccountsClass();

  constructor(
    private httpService: HttpService,
    private loginService: LoginService
  ) { }
  
  public getAccounts(isAll = false): Observable<any> {
    return this.httpService.GetRequest(api.getAccounts + `?ep=${isAll}`).pipe(
      map(value => {
        if (!value.success) {
          this.loginService.logout();
          return;
        }
        return value;
      }, () => {
        this.loginService.logout();
      })
    );
  }

  public addAccountsToEstatePlan(accounts): Observable<any> {
    return this.httpService.PutRequest(api.addAccountsToEstatePlan, accounts);
  }

  public getAccount(assetId: number): Observable<any> {
    return this.httpService.GetRequest(api.getAccount + assetId);
  }

  public createAccount(asset: any): Observable<any> {
    return this.httpService.PostRequest(api.createAccount, asset);
  }

  public updateAccount(asset: any, assetId: number): Observable<any> {
    return this.httpService.PutRequest(api.saveChangesAccount + assetId, asset);
  }

  public removeAccount(assetId: number): Observable<any> {
    return this.httpService.DeleteRequest(api.removeAccount + assetId);
  }

}


