import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { LoginService } from './../../services/loginService/login.service';

import { Subscription } from 'rxjs';

@Component({
  selector: 'app-unverified-user',
  templateUrl: './unverified-user.component.html',
  styleUrls: ['./unverified-user.component.scss']
})
export class UnverifiedUserComponent implements OnInit {
  subscriptions: Subscription[] = [];
  // newError='We sent you "Email Verification Link" that is only valid for 7-days. ';
  emailVerification = '';
  verificationSuccessMsg = '';
  verificationErrorMsg = '';
  forgetPasswordEmail = '';
  buttonText = 'Resend Verification Link';


  constructor( private loginService : LoginService,
    public dialogRef: MatDialogRef<UnverifiedUserComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      if (data != null) {
        this.verificationErrorMsg  = data.verificationErrorMsg; 
        this.emailVerification = data.emailVerification;
    }
  }

  ngOnInit() {
  }

  verifyEmail(element) {
    if(this.buttonText == "Resend Verification Link")
    {
             this.verificationErrorMsg = '';
            const obj = {
              email: this.emailVerification
            }
            this.loginService.newemailVerification(obj).subscribe(res => {
              console.log(res)
              if (!res.success) {
                 this.verificationSuccessMsg
                element.textContent = 'Close';
                this.buttonText = 'Close';
            } else {
                this.verificationErrorMsg =res.error;
              }
            }, err => {
              const errObj = JSON.parse(err._body);
              this.verificationErrorMsg = errObj.error;
            });
    }      
              else if (this.buttonText=="Close") {
              this.closedialog()
            }
         }


  closedialog() {
    this.dialogRef.close();
  }

}
