// 3rd party libs
import { Component, OnInit, Input,  } from '@angular/core';

@Component({
	selector: 'form-errors-messages',
	templateUrl: './form-errors-messages.component.html',
  styleUrls: ['./form-errors-messages.component.scss']
})
export class FormErrorsMessagesComponent implements OnInit {

  @Input() public input: any;
  @Input() public pattern: string;
  public patternError: string;

	constructor() {}

	ngOnInit() {
    switch (this.pattern) {
      case "number":
        this.patternError = 'error_number';
        break;
      case "htmlLink":
        this.patternError = 'input.valid_link';
        break;
      case "phone":
        this.patternError = 'Invalid format';
        break;
      case "currency":
        this.patternError = 'error_currency';
        break;
    }
  }

}