import { Component, OnInit } from '@angular/core';
import { api } from "../../../services/api";
import { HttpService } from "../../../services/http.service";

@Component({
  selector: 'app-full-component',
  templateUrl: './full-component.component.html',
  styleUrls: ['./full-component.component.scss']
})
export class FullComponent implements OnInit {

  title = 'clocr';
  sidnav :string = 'true';
  isNavbar: Boolean = true;
  constructor(private httpService: HttpService) { }

  ngOnInit() {
    // localStorage.setItem("sidenav", 'false');
    this.sidnav = localStorage.getItem("sidenav")
    var useDetails = JSON.parse(sessionStorage.getItem("clocr_user"));
    if(useDetails != null){

      if(useDetails.onboard_complete == "YES"){
        this.isNavbar = true;
      } else {
        this.isNavbar = false;
      }
    } else {

      this.httpService.GetRequest(api.getUserDetails)
      .subscribe(res => {
        console.log(res);
        if (res['success']) {
          if(res['data'].onboard_complete == "YES"){
            this.isNavbar = true;
          } else {
            this.isNavbar = false;
          }  
        
        }
        });

    }

  }
}
