  saveCaretaker(caretakerObj: object) {
    this.hs.PostRequest(api.saveCaretaker, caretakerObj).subscribe(res => {
      if (res['success']) {
        const caretaker = this.legalService.caretaker$.getValue();
        // caretaker.push(res['data']);
        // this.legalService.caretaker$.next(caretaker);

        this.legalService.getCareTackerCount();
        
        this.legalService.caretaker$.next(this.legalService.getCareTacker());
        this.toaster.showSuccess('Assignee successfully added');

      } else {
        if (res['data'] == 'logout') {
          this.loginService.logout();
        }
        this.toaster.showError(res['error']);
      }
    });

  }

