  getUserDetails() {
   if(sessionStorage.getItem("clocr_user")===null){
    this.hs.GetRequest(api.getUserDetails)
      .subscribe(res => {
        console.log(res);
        if (res['success']) {
          this.userDetails$.next(res['data']);
          sessionStorage.setItem("clocr_user", JSON.stringify(res['data']));
        } else {
          if (res['data'] == 'logout') {
            this.loginService.logout();
          }

        }
      });
    }else{
      this.userDetails$.next(JSON.parse(sessionStorage.getItem("clocr_user")));
    }
  }

  getUserDetailsForGuard() : Observable<boolean> {
    var subject = new Subject<boolean>();
    this.hs.GetRequest(api.getUserDetails)
        .subscribe(async (res) => {
          console.log(res);
          if (res['success']) {
            sessionStorage.setItem("clocr_user", JSON.stringify(res['data']));
            
            if(res['data'].onboard_complete == "NO"){
              subject.next(true);
            } else {
              this.router.navigate(['/dashboard']);
              subject.next(false);
            }
          }
          else {
            if (res['data'] == 'logout') {
              this.loginService.logout();
              subject.next(false);
            }
          }
          
        });

    return subject.asObservable().first();
  }

