import { Component, OnInit } from "@angular/core";
import { OnboardingService } from "src/app/services/onboarding/onboarding.service";
import { Router } from "@angular/router";
import { MAT_DIALOG_DATA, MatDialogRef, MatDialogConfig, MatDialog } from '@angular/material';
import { DynamicGrid } from './dynamicgrid.model';
interface Onboarding {
  userType: number;
  step: number;
}
const ONBOARDING_STEPS = 5;
@Component({
  selector: "app-onboarding",
  templateUrl: "./onboarding.component.html",
  styleUrls: ["./onboarding.component.scss"]
})
export class OnboardingComponent implements OnInit {
  isMobileLayout: any;
  firstName: any;
  lastName: any;
  email: any;
  relationship: any;
  steps = [
    {
      label: "step 1",
      value: 1
    },
    {
      label: "step 2",
      value: 2
    },
    {
      label: "step 3",
      value: 3
    },
    {
      label: "step 4",
      value: 4
    }
  ];
  personalisationOptions = [
    {
      name: "Well Established",
      image: "../assets/images/onboarding/family.png",
      features: [
        "Settled in your career",
        "Have a family, teen/adult kids",
        "Senior parents"
      ],
      value: 1,
      a: "img1"
    },
    {
      name: "Young Professional",
      image: "../assets/images/onboarding/younprof.png",
      features: [
        "Getting started with family",
        "Minor kids",
        "Well established or senior parents"
      ],
      value: 2,
      a: "img1"
    },
    {
      name: "Young Adult",
      image: "../assets/images/onboarding/yougadult.png",
      features: [
        "Starting off in your career",
        "An adult student",
        "Well established parents, grand parents"
      ],
      value: 3,
      a: "img1"
    }
    // ,
    // {
    //   name: "Business Owner",
    //   image: "../assets/images/onboarding/bussiness.png",
    //   features: [
    //     "Primarily for your business needs",
    //     "Can also use it for your family"
    //   ],
    //   value: 4,
    //   a:"img1"
    // }
  ];

  personalisationType = "";
  onboarding: Onboarding = { userType: 1, step: 0 };
  onboardingDefaults: any = [];
  drawers: any = [];
  constructor(
    private onboardingService: OnboardingService,
    private router: Router,
    public dialogRef: MatDialogRef<OnboardingComponent>,
  ) {
  }
  dynamicArray: Array<DynamicGrid> = [];
  newDynamic: any = {};
  promoData: any;

  ngOnInit() {
    this.newDynamic = { firstName: "", lastName: "", email: "", relation: "" };
    this.dynamicArray.push(this.newDynamic);
    this.onboardingService.getOnboardingStatus().subscribe(res => {
      console.log('JSON data-> ' + JSON.stringify(res));
      if (res["success"]) {
        if (res.data.onboard_step > ONBOARDING_STEPS) {
          this.dialogRef.close()
        }
        this.personalisationType =
          res.data.onboard_category &&
          this.personalisationOptions[res.data.onboard_category].name;
        this.onboarding.userType =
          res.data.onboard_category &&
          this.personalisationOptions[res.data.onboard_category].value;
        this.onboarding.step =
          res.data.onboard_step > 0 ? res.data.onboard_step : 0;
      }

      this.onboardingService.onboardingDefaults().subscribe(res => {
        if (res["success"]) {
          const data = JSON.parse(res.data);
          this.onboardingDefaults = data.data;
          this.drawers = this.onboardingDefaults[
            this.onboarding.userType - 1
          ].drawers;
        }
      });
    });
  }

  addRow(index) {
    this.newDynamic = { firstName: "", lastName: "", email: "", relation: "" };
    this.dynamicArray.push(this.newDynamic);

    console.log(this.dynamicArray);
    return true;
  }

  onClose(){
    window.location.reload()
  }
  customTrackBy(index: number, obj: any): any {
    console.log(index)
    return index;
  }

  deleteRow(index) {
    if (this.dynamicArray.length == 1) {

      return false;
    } else {
      this.dynamicArray.splice(index, 1);

      return true;
    }
  }
  a: any;
  onSwitchDrawer(e, step) {
    this.personalisationType = this.personalisationOptions[step].name;
    this.onboarding.userType = this.personalisationOptions[step].value;
    this.drawers =
      this.onboardingDefaults[this.onboarding.userType - 1] &&
      this.onboardingDefaults[this.onboarding.userType - 1].drawers;

    this.onboardingService
      .saveOnboardingStatus({
        onboard_category: this.personalisationOptions[step].value,
        onboard_complete: "YES",
        onboard_step: 5
      })
      .subscribe(data => {
        console.log("saved", data);
      });
  }
  // onSubmit(){
  //   console.log("testing",this.dynamicArray);
  // }
  // onSubmit(){
  //   this.onboardingService.saveBeneficiaryStatus(this.dynamicArray).subscribe(data =>{
  //     this.onNext();
  //     console.log("siva new data", data);
  //   });
  // }
  isClicked = false;
  errorMsg = '';
  onSubmit() {
    if (this.isClicked) {
      return false;
    }
    this.errorMsg = '';
    if (this.firstName === '') {
      this.errorMsg = 'Please Enter First Name';
      return false;
    }
    if (this.lastName === '') {
      this.errorMsg = 'Please Enter Last Name';
      return false;
    }
    this.onboardingService.saveBeneficiaryStatus({ "contacts": this.dynamicArray });
    this.onNext()
  }

  onSelectType(e, step) {
    this.personalisationType = this.personalisationOptions[step].name;
    this.onboarding.userType = this.personalisationOptions[step].value;
    this.drawers =
      this.onboardingDefaults[this.onboarding.userType - 1] &&
      this.onboardingDefaults[this.onboarding.userType - 1].drawers;
    this.onboarding.step = this.onboarding.step + 1;
    this.onboardingService
      .saveOnboardingStatus({
        onboard_category: this.personalisationOptions[step].value,
        onboard_complete: "YES",
        onboard_step: 1
      })
      .subscribe(data => {
        console.log("saved", data);
      });
  }

  changeStatus = (parentIndex, index, e) => {
    const userType = this.onboarding.userType - 1;
    const defaults = this.onboardingDefaults[userType];
    defaults.drawers[parentIndex].folders[index].active =
      defaults.drawers[parentIndex].folders[index].active === "YES"
        ? "NO"
        : "YES";
    this.onboardingDefaults[userType] = defaults;
    this.drawers =
      this.onboardingDefaults[userType] &&
      this.onboardingDefaults[userType].drawers;
  };

  onBack = () => {
    this.onboarding.step = this.onboarding.step - 1;
  };


  onNext = () => {
    this.onboarding.step = this.onboarding.step + 1;
    if (this.onboarding.step === 5) {
      this.onboardingService
        .saveOnboardingDefaults(
          this.onboardingDefaults[this.onboarding.userType - 1]
        )
        .subscribe(res => {
          if (res["success"]) {
            console.log("saved defaults");
          }
        });
    }

    this.onboardingService
      .saveOnboardingStatus({
        onboard_category: this.onboarding.userType,
        onboard_complete: "YES",
        onboard_step: this.onboarding.step
      })
      .subscribe(data => {
        console.log("saved", data);
        if (this.onboarding.step > ONBOARDING_STEPS) {
          window.location.reload();
        }
      });

    if (this.onboarding.step > ONBOARDING_STEPS) {
      const user = JSON.parse(sessionStorage.getItem("clocr_user"))
      user.onboard_complete = "YES"
      sessionStorage.setItem("clocr_user", JSON.stringify(user));
      this.dialogRef.close()
    }
  };
}
