import { Injectable } from '@angular/core';
import { api } from '../api';
import {Observable, of} from 'rxjs'
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { HttpService } from '../http.service';
import { HttpClient } from '@angular/common/http';
import { from } from 'rxjs';
import { Router } from '@angular/router';
import { LoginService } from '../loginService/login.service';
import { LegalService } from '../legalService/legal.service';
import { ToasterService } from '../toaster.service';
import { catchError } from 'rxjs/operators';
// import { HttpService } from './http.service';


@Injectable()
export class DashboardService {
 
   base = 'https://dev.clocr.com';
   sample : any = "assas";
   //sidnav : any = false;
  public sidnav: any = false;

  constructor(private router: Router,
    private hs: HttpService, 
    private loginService: LoginService,
    private legalService : LegalService,
  private toaster: ToasterService) { }
    
  getDashboardAssets() {
    return this.hs.GetRequest(api.getDashboardAssets).pipe(data => data, catchError(error => of(error)));
  }
  getDashboardMpoa() {
    return this.hs.GetRequest(api.getDashboardMpoa).pipe(data => data, catchError(error => of(error)));
  }
  getDashboardCaretakers() {
    return this.hs.GetRequest(api.getDashboardCaretakers).pipe(data => data, catchError(error => of(error)));
  }
  getDashboardCurrentCaretaker() {
    return this.hs.GetRequest(api.getDashboardCurrentCaretaker).pipe(data => data, catchError(error => of(error)));
  }

}
